.parentContainer{
    max-width:100%;
    padding:20px;
    margin:20px;
    background : #d8c3a5;
}
.aboutBox{
    display:flex;
    gap:20px;
    align-items:center;
    background:#eae7dc;
    padding:20px;
}
.imageBox{
    border:1px solid #976c30;
    padding:10px;
    background : #eae7dc;
    border-radius : 50%;
}
.image{
    border-radius: 50%;
}
.h4{
    margin:5px;
    color: #8e8d8a;
    font-family :'Courier New', Courier, monospace;
}
.h3{
    margin:5px;
    color : #e98074;
    font-family :'Courier New', Courier, monospace;
}
.h1{
    margin:5px;
    font-size: 40px;
    color : #976c30;
}

@media all and (max-width : 600px ) and (min-width : 200px ){
    .aboutBox{
        flex-direction : column;
    }
    .h1,.h3,.h4{
        text-align:center
    }
    
}