.navbar {
    max-width:100%;
    padding:0px 15px;
    display : flex;
    align-Items : center;
    justify-content: space-between;
    flex-direction : row;
    border-bottom:5px solid #d8c3a5;
}
.leftNav{
    display : flex;
    align-items:center;
}
.logo {
    height:70px;
}
.heading{
    font-family:Georgia, 'Times New Roman', Times, serif;
    font-weight:500;
    color : #e85a4f
}
.navButton{
    border : 1px solid #e98074;
    padding : 10px 15px;
    font-size:16px;
    background:none;
    border-radius:5px;
    color : #e98074;
    cursor:pointer;
}
.navButton:hover{
    box-shadow: #d8c3a5 0px 3px 3px, #d8c3a5 0px 3px 3px;
}
.largeNav{
    display : flex;
    gap:15px;
}
.smallNav{
    display : none;
}
.menu{
    min-width:200px;
    background : #eae7dc;
    border : 1px solid #e98074;
}
.menuItem{
    
    padding:10px;
    font-size:16px;
    border:none;
    background:none;
    justify-content:center;
    cursor:pointer;
}
.menuItem:hover{
    background : #d8c3a5;
    color : #e85a4f
}
@media all and (max-width : 950px) and (min-width: 200px){
    .largeNav{
        display : none;
    }
    .smallNav{
        display : block;
    }
}
@media all and (max-width : 350px) and (min-width: 200px){
    
    .logo{
        display : none;
    }
}